/* Reset default padding and margin */
/*...................... COMMON CSS............... */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Nunito Sans", sans-serif;
}
/* Set padding for main content area */
.main {
  padding: 5px 100px ;
}
.main2 {
  padding: 5px 100px ;
  overflow: hidden;
  background-color: #ffffff;
}
/*............... COMMON CSS ENDS .................*/

/* ............................TOPBAR CSS......................  */
/* Top bar styles */
.topbar {
  display: flex;
  justify-content: space-between;
  background-color: #151515;
  color: #fff;
  z-index: 1;
}

/* Left side of top bar */
.topbar__left {
  display: flex;
  justify-content: center;
  align-items: center;
}

.topbar__left p {
  font-size: 14px;
  margin-left: 10px;
  padding-right: 20px;
  display: flex;

  justify-content: center;
  align-items: center;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}
.topbar__left span {
  margin-left: 10px;
}
/* Right side of top bar */
.topbar__right {
  display: flex;
  align-items: center;
}

.topbar__right p {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 5px;
  padding: 8px;
  border-radius: 50px;
  font-size: 14px;
}

/* Social media icons */
.tweet {
  background-color: #1da1f2;
}

.linked {
  background-color: #0077b5;
}

.insta {
  background-color: #e4405f;
}

.fb {
  background-color: #1976d2;
}
@media only screen and (max-width: 767px) {
  .topbar {
    display: none;
  }
}
